import { Icon } from "@iconify/react";
import React from "react";
function SkillItem(props) {
  return (
    <>
      <div
        className="skill col"
        title={props.title}
        style={{
          background: `linear-gradient(0deg, rgba(220, 220, 220,1) 0%, rgba(220, 220, 220,1) ${props.level}%, rgba(255,255,255,1) ${props.level}%)`,
        }}
      >
        {props.icon_type === "fa" ? <i className={props.icon_class}></i> : <Icon icon={props.icon_class} />}
      </div>
    </>
  );
}

export default SkillItem;
