import React from "react";
import logo from "../img/logo.png";
import AnchorLink from "react-anchor-link-smooth-scroll";
import { Link } from "react-router-dom";
function ProjectsNav(props) {
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
        <AnchorLink className="navbar-brand js-scroll-trigger" to="/">
          <span className="d-block d-lg-none">Graham Coulby</span>
          <span className="d-none d-lg-block">
            <img className="img-fluid img-profile mx-auto mb-2" src={logo} alt="" />
          </span>
        </AnchorLink>
        <button
          className="navbar-toggler collapsed"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link to="/" className="nav-link js-scroll-trigger">
                <i className={"fa fa-home"}></i> Home
              </Link>
            </li>
            {props.showTags ? (
              <>
                <li className="nav-item">
                  <span className="nav-link js-scroll-trigger" onClick={() => props.setTag("all")}>
                    <i className={"fa fa-eye"}></i> Show All
                  </span>
                </li>
                <li className="nav-item">
                  <hr />
                </li>
                <ul className="navbar-nav navbar-nav-inner">
                  {props.keyWords.map((keyword, i) => (
                    <li key={`projects-nav-item-${i}`} className="nav-item">
                      <span className="nav-link js-scroll-trigger" onClick={() => props.setTag(keyword)}>
                        <i className={"fa fa-tag"}></i> {keyword}
                      </span>
                    </li>
                  ))}
                </ul>
              </>
            ) : (
              <li className="nav-item">
                <Link to="/portfolio" className="nav-link js-scroll-trigger">
                  <i className={"fa fa-angle-left"}></i> Go Back
                </Link>
              </li>
            )}
          </ul>
        </div>
      </nav>
    </>
  );
}

export default ProjectsNav;
