import React, { useState } from "react";
import "../styles/css-compiled/projects.css";
import ProjectItem from "./project-item";
import ProjectsNav from "./projectNav";
import "bootstrap/dist/js/bootstrap.js";

function Portfolio(props) {
  const [tag, setTag] = useState("all");
  return (
    <>
      <div className="modular">
        <ProjectsNav keyWords={props.keyWords} setTag={setTag} showTags={true} />
        <section className="resume-section" id="o">
          <div className="resume-section-content">
            {tag === "all"
              ? props.projects.map((project) => <ProjectItem key={project.title} {...project} />)
              : props.projects.filter((project) => project.keywords.includes(tag)).map((project) => <ProjectItem key={project.title} {...project} />)}
          </div>
        </section>
      </div>
    </>
  );
}

export default Portfolio;
