import React, { useState } from "react";
import { v4 } from "uuid";
import PublicationItem from "./publication-item";
import _publications from "../lists/publications.js";
function Publications() {
  const [publicationItems, setPublicationItems] = useState(_publications);

  const setShowAbstract = (title) => {
    console.log(title);
    setPublicationItems((prev) =>
      prev.map((item) => {
        if (item.title === title) {
          return { ...item, showAbstract: !item.showAbstract };
        }
        return item;
      })
    );
  };

  return (
    <>
      <section className="resume-section" id="publications">
        <div className="resume-section-content">
          <h2 className="mb-5">Publications</h2>
          {publicationItems
            .sort((a, b) => new Date(b.publicationDate) - new Date(a.publicationDate))
            .map((item) => (
              <PublicationItem key={v4()} {...item} setShowAbstract={setShowAbstract} />
            ))}
        </div>
      </section>
    </>
  );
}

export default Publications;
