import Home from "./components/home";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Main from "./components/main";
import Project from "./components/project";
import { useEffect, useState } from "react";
import Portfolio from "./components/portfolio";
import _projects from "./lists/projects";
import _socialIcons from "./lists/social-icons";

function App() {
  const [socialIcons, setSocialIcons] = useState(_socialIcons);
  const [projects, setProjects] = useState([]);
  const [keyWords, setKeyWords] = useState([]);
  useEffect(() => {
    setProjects(_projects.sort(() => Math.random() - 0.5));
    const keywords = [];
    _projects.forEach((project) => {
      project.keywords.forEach((keyword) => {
        if (!keywords.includes(keyword)) {
          keywords.push(keyword);
        }
      });
    });
    setKeyWords(keywords.sort((a, b) => a.localeCompare(b)));
  }, []);
  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home socialIcons={socialIcons} />} />
          <Route path="/cv" element={<Main socialIcons={socialIcons} />} />
          <Route path="/portfolio" element={<Portfolio projects={projects} keyWords={keyWords} />}></Route>
          {projects.map((project, index) => (
            <Route key={`project-routes-${index}`} path={`/portfolio/${project.slug}`} element={<Project key={project.slug} {...project} />} />
          ))}
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
