import React from "react";
function PublicationItem({ title, publicationDate, publicationType, publisher, doi, abstract, keywords, authorType, showAbstract, setShowAbstract }) {
  const dtOptions = { year: "numeric", month: "long", day: "numeric" };
  return (
    <>
      <div className="d-flex flex-column flex-md-row justify-content-between resume-section-item">
        <div className="flex-grow-1">
          <h3 className="mb-0 ">{title}</h3>
          <h5 className="mr-2">{publicationType}</h5>
          <h5 className="mr-2">Published {publicationDate.toLocaleString("en-GB", dtOptions)}</h5>
          <h5>{authorType}</h5>
          <div className="subheading mb-3">{publisher}</div>
          {showAbstract ? (
            <div>
              <button className="btn btn-sm btn-outline-dark mb-4 " onClick={() => setShowAbstract(title)}>
                Hide Abstract
              </button>
              <p>
                <b>Abstract:</b> {abstract}
              </p>
            </div>
          ) : (
            <button className="btn btn-sm btn-outline-dark mb-4 " onClick={() => setShowAbstract(title)}>
              Show Abstract
            </button>
          )}
          <div className="mb-2">
            {keywords.map((keyword, index) => (
              <div key={`keywords-list-${index}`} className="badge bg-light text-dark keyword">
                {keyword}
              </div>
            ))}
          </div>
          <a className="doi text-dark" target="_blank" rel="noopener noreferrer" href={doi}>
            {doi}
          </a>
        </div>
      </div>
    </>
  );
}

export default PublicationItem;
