import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import ReactMarkdown from "react-markdown";
import gfm from "remark-gfm";
import rehypeRaw from "rehype-raw";
import metadataParser from "markdown-yaml-metadata-parser";
import ProjectsNav from "./projectNav";

function Project(props) {
  const [post, setPost] = useState(null);
  const fetchContent = () => {
    fetch(`/projects/${props.slug}/item.md`)
      .then((response) => response.text())
      .then((text) => {
        var result = metadataParser(text);
        setPost(result.content);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  useEffect(() => {
    fetchContent();
  }, []);

  return (
    <div className="modular">
      <ProjectsNav />
      <section className="resume-section" id="o">
        <div className="resume-section-content">
          <h3>{props.title}</h3>
          <p>{props.description}</p>
          <p>
            <Link to="/portfolio">Portfolio</Link> {`>`} {props.title}
          </p>
          <ReactMarkdown children={post} remarkPlugins={[gfm]} rehypePlugins={[rehypeRaw]} />
        </div>
      </section>
    </div>
  );
}

export default Project;
