import React from "react";
import { v4 } from "uuid";
import { Link } from "react-router-dom";

function ProjectItem(props) {
  return (
    <>
      <div className="d-flex flex-column flex-md-row justify-content-between resume-section-item">
        <div className="flex-grow-1">
          <div className="d-flex">
            <div className="crop">
              <Link to={props.slug} className="">
                <img className="cropped-image" alt="feature for the project" src={`/projects/${props.slug}/feature.png`} />
              </Link>
            </div>
            <div>
              <Link to={props.slug} className="" style={{ textDecoration: "none" }}>
                <h3 className="mb-0">{props.title}</h3>
              </Link>
              <p>{props.description}</p>
              <div className="mb-2">
                {props.keywords.map((keyword) => (
                  <span className="badge bg-secondary me-1" key={v4()}>
                    {keyword}
                  </span>
                ))}
              </div>
              <Link to={props.slug} className="btn btn-dark">
                More&nbsp;<i className="fa fa-angle-right"></i>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ProjectItem;
