import React, { useState } from "react";
import logo from "../img/logo.png";
import AnchorLink from "react-anchor-link-smooth-scroll";
import NavItem from "./nav-item";
import { v4 } from "uuid";
import { Link } from "react-router-dom";
import _navItems from "../lists/nav.js";

function Nav() {
  const [navItems] = useState(_navItems);
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
        {/* <AnchorLink className="navbar-brand js-scroll-trigger" to="/"> */}
        <Link to="/" className="navbar-brand js-scroll-trigger">
          <span className="d-block d-lg-none">Graham Coulby</span>
          <span className="d-none d-lg-block">
            <img className="img-fluid img-profile mx-auto mb-2" src={logo} alt="" />
          </span>
        </Link>
        {/* </AnchorLink> */}
        <button
          className="navbar-toggler collapsed"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link to="/" className="nav-link js-scroll-trigger">
                <i className={"fa fa-home"}></i> Home
              </Link>
            </li>
            {navItems.map((item) => (
              <NavItem key={v4()} {...item} />
            ))}
          </ul>
        </div>
      </nav>
    </>
  );
}

export default Nav;
