import React, { useState } from "react";
import logo from "../img/logo.png";
import "../styles/src/home.css";
import { Link } from "react-router-dom";
import SocialIcon from "./socialIcons";

function Home(props) {
  const [fadeIn] = useState(true);
  const [showHome] = useState(true);

  return (
    <>
      <div className="">
        {showHome && (
          <>
            <header className="App-header">
              <div className="row">
                <div className={"app-start" + (fadeIn ? " fade-in" : "")}>
                  <img src={logo} className="App-logo" alt="logo" />
                  <Link className="btn btn-outline-light mt-3" to="/cv">
                    CV
                  </Link>
                  <Link className="btn btn-outline-light mt-3" to="/portfolio">
                    Portfolio
                  </Link>
                </div>
              </div>
              <div className={"row app-start" + (fadeIn ? " fade-in" : "")}>
                <div className="social-icons social-icons-dark">
                  {props.socialIcons.map((icon) => (
                    <SocialIcon key={icon.title} {...icon} />
                  ))}
                </div>
              </div>
            </header>
          </>
        )}
      </div>
    </>
  );
}

export default Home;
